// @mui
import { useEffect, useState } from 'react';
import { alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { DatePicker } from '@mui/x-date-pickers';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Grid, InputAdornment, InputLabel, Menu, MenuItem, Modal, Select, TextField } from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { color, decryptToken, formatObj, generateOfferCode, states } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import { useLocation, useNavigate } from 'react-router';
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs/custom-breadcrumbs';
import { paths } from 'src/routes/paths';

const initialEmployeeData = {
    id: '',
    code: '',
    name: '',
    mobileNumber: '',
    dateOfBirth: null,
    address: {
        line1: '',
        line2: '',
        city: '',
        state: '',
        pincode: '',
    },
};

const initialCustomerErr = {
    name: false,
    email: false,
    mobileNumber: false,
    type: false,
    company: {
        name: false,
        gstNumber: false,
        billingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
        shippingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
    },
};

// ----------------------------------------------------------------------
const decryptedToken = localStorage.getItem(constants.keyUserToken);
const accessToken = decryptToken(decryptedToken);
const config = {
    headers: {
        Authorization: `Bearer ${accessToken}`
    }
};

export default function EmployeeDetailsView() {
    const [isLoading, setIsLoading] = useState(false);
    const [salesHistory, setSetHistory] = useState([]);

    const settings = useSettingsContext();
    const location = useLocation();
    const employeeData = location?.state;
    console.log("ðŸš€ ~ EmployeeDetailsView ~ employeeData:", employeeData)
    employeeData.dateOfBirth = dayjs(employeeData.dateOfBirth);

    const generateAddress = (address) => {
        console.log('Generating address', address);
        const addressParts = [];
        if (address.line1) addressParts.push(address.line1);
        if (address.line2) addressParts.push(address.line2);
        if (address.city) addressParts.push(address.city);
        if (address.state) addressParts.push(address.state);
        if (address.pincode) addressParts.push(address.pincode);
        return addressParts.join(', ');
    };

    // Call the api and get data from sales API
    const fetchSalesData = async () => {
        try {
            const response = await axios.post(endpoints.employee.getSalesHistory, { employeeId: employeeData._id }, config);
            console.log("ðŸš€ ~ fetchSalesData ~ response:", response)
            if (response) {
                setIsLoading(false);
                setSetHistory(response.data.data);
            }
        } catch (err) {
            setIsLoading(false);
        }
    };

    useEffect(() => {
        fetchSalesData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const columns = [
        {
            name: 'Customer Name',
            selector: row => row.customerId.name,
        },
        {
            name: 'Product Name',
            selector: row => row.products.length > 1 ? `${row.products[0].name}, & ${row.products.length - 1} more` : row.products[0].name,
        },
        {
            name: 'Sales Amount',
            selector: row => formatObj.format(row.totalAmount),
        },
        {
            name: 'Invoice Date',
            selector: row => dayjs(row.createdAt).format('DD/MM/YYYY'),
        },
        {
            name: 'Payment Mode',
            selector: row => row.payments[row.payments.length - 1].paymentID.paymentMode,
        }
    ];

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <CustomBreadcrumbs
                heading='Employee Details'
                links={[
                    {
                        name: 'Employee Master',
                        href: paths.dashboard.employee.list,
                    },
                    {
                        name: `${employeeData.name}`,
                    },
                ]}
                sx={{
                    mb: { xs: 3, md: 5 },
                }}
            />

            <Box
                sx={{
                    mt: 2,
                    width: 1,
                    p: 2,
                    borderRadius: 2,
                    bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
                    border: (theme) => `dashed 1px ${theme.palette.divider}`,
                }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography variant='h6'>Basic Details</Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Name'
                            value={employeeData.name}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Employee Code'
                            value={employeeData.code}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Mobile Number'
                            value={employeeData.mobileNumber}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label="Date of Birth"
                            defaultValue={dayjs(employeeData.dateOfBirth).format('DD/MM/YYYY')}
                            disabled
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={8}>
                        <TextField
                            label='Address'
                            multiline
                            rows={2}
                            value={generateAddress(employeeData.address)}
                            fullWidth
                            disabled
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box
                sx={{
                    mt: 2,
                    width: 1,
                    p: 2,
                    borderRadius: 2,
                    bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
                    border: (theme) => `dashed 1px ${theme.palette.divider}`,
                }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography variant='h6'>Sales History</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <DataTable
                            progressPending={isLoading}
                            columns={columns}
                            data={salesHistory}
                            pagination
                        />
                    </Grid>

                </Grid>
            </Box>

        </Container>
    );
}
